.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  margin: 0;
  padding: 16px;
  display: flex;
  position: relative;
}

.card__img {
  justify-content: center;
  width: 160px;
  height: 134px;
  margin: 16px 3px 0;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  align-items: flex-end;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-top: 24px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-numder {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
}

.rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.stars {
  justify-content: center;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  justify-content: space-between;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

